.hero {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-block: 100px !important;
    background: #00000020 url('../../assets/img/hero/hero-bg.webp') no-repeat center top / cover;

    @media (max-width: 768px) {
        padding-top: 70px;
        gap: 34px;
    }

    .hero__wrapper {}

    &__content {
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: flex-start;
        gap: 24px;

        @media (max-width: 768px) {
            display: flex;
            flex-direction: column-reverse;
            align-items: flex-start;
            // grid-template-columns: 1fr;
        }
    }

    &__left {
        padding-block: 60px;
        display: flex;
        flex-direction: column;
        gap: 48px;
        align-items: flex-start;
        flex: 1;
        height: 100%;
        justify-content: space-between;


        @media (max-width: 1200px) {
            padding: 0;
            gap: 0;
        }
    }

