    &__titleMobile {
        color: $white !important;
        margin-bottom: 35px;
        text-align: left;
        display: none;

        @media (max-width: 768px) {
            display: block;
        }
    }

    &__titleDesktop {
        color: $white !important;
        text-align: left;
        margin-bottom: 35px;

        @media (max-width: 768px) {
            display: none;
        }
    }

    &__btn {
        margin-top: auto;
        background-color: var(--bg);
        // border: 2px solid red;
        padding: 12px 36px;
        color: var(--black);

        border-radius: 10px;
        transition: all 0.3s ease-in-out;


        &:hover {
            background-color: #921AAF;
            border-color: #921AAF;
            color: var(--white);
        }
    }
}